import { useEffect } from 'react';
import { useForm } from 'react-hook-form';
import { useRecoilState, useRecoilValue, useSetRecoilState } from 'recoil';
import styled from 'styled-components';
import { categoryState, Conditions, modalState, toDoState } from '../atoms';

const AddToDoModal = styled.div`
	position: fixed;
	top: 0;
	left: 0;
	width: 100%;
	height: 100%;
	display: flex;
	justify-content: center;
	align-items: center;
	& > div {
		width: 300px;
		padding: 10px;
		border-radius: 10px;
		background: ${(props) => props.theme.bgColor};
		border: 1px solid ${(props) => props.theme.textColor};
		text-align: right;
	}

	& > div > button {
		width: 30px;
		height: 30px;
		font-size: 20px;
		background: transparent;
		border: none;
		outline: none;
		color: #fff;
	}
`;

const AddToDoForm = styled.form`
	display: flex;
	flex-direction: column;

	select {
		flex: 0 0 40px;
		background: ${(props) => props.theme.bgColor};
		border: none;
		outline: none;
		color: ${(props) => props.theme.textColor};
	}
	input {
		width: 100%;
		height: 30px;
		background: transparent;
		border: 1px solid ${(props) => props.theme.textColor};
		padding: 0 5px;
		outline: none;
		color: ${(props) => props.theme.textColor};
		margin: 5px auto;
	}
	button {
		flex: 0 0 40px;
		height: 100%;
		font-size: 30px;
		padding: 0;
		color: ${(props) => props.theme.textColor};
		background: transparent;
		border: none;
		outline: none;
	}
`;

/* react hook form ->      . validation   */
interface IForm {
	toDo: string;
	categoryId: number;
}

function CreateToDo() {
	/* useState      */
	const [toDos, setToDos] = useRecoilState(toDoState);
	const category = useRecoilValue(categoryState);
	const setModalState = useSetRecoilState(modalState);
	/*
  - register   useState, onChange     
  - ...register('toDo') : es6. register    props 
  - watch : form       
  - handleSubmit : onSubmit   , validation   
  - formState.errors : error            . 
  - setError :   
  */
	const { register, handleSubmit, setValue } = useForm<IForm>();
	const onValid = async ({ toDo, categoryId }: IForm) => {
		await setToDos((prev) => [{ text: toDo, id: Date.now(), category: categoryId, condition: Conditions.YET }, ...prev]);
		setValue('toDo', '');
		setModalState({ status: false, type: '' });
	};

	useEffect(() => {
		localStorage.setItem('toDo', JSON.stringify(toDos));
	}, [toDos]);

	return (
		<AddToDoModal>
			<div>
				<button
					className='closeBtn'
					onClick={() => {
						setModalState({ status: false, type: '' });
					}}
				>
					
				</button>
				<AddToDoForm onSubmit={handleSubmit(onValid)}>
					<select {...register('categoryId')}>
						{Object.values(category).map((item) => (
							<option value={item.id} key={item.id}>
								{item.name}
							</option>
						))}
					</select>
					<input {...register('toDo', { required: true })} placeholder='Write a to do' />
					<input type='submit' value='add to do' />
				</AddToDoForm>
			</div>
		</AddToDoModal>
	);
}
export default CreateToDo;
